.contact-details {
    background-color: #9994;
    display: flex;
    height: 200px;
    align-items: center;
    gap: 30px;
    position: relative;

    img {
        height: 150px;
        width: 150px;
        padding: 10px;
    }
    .contact-info {
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: 10px;
        font-size: 1.1em;

        .back-button {
            position: absolute;
            bottom: -35px;
            left: 50%;
            transform: translateX(-50%);
            padding: 6px 20px;
            border-radius: 0 0 24% 24%;

            background-color: #9995;
            color: #333;
        }
    }
}
